.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
header {
  text-align: center;
  position: relative;
  z-index: 999;
}
header h1 {
    margin: 60px 0  30px 0;
    font-size: 42px;
    font-weight: 900;
    color: #fff;
}
header ul {
    width: 600px;
    margin: 0 auto;
    height: 50px;
    position: absolute;
    top: 30px;
    left: 0;
    right: 0;
    z-index: 999;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
header ul li a {
      font-family: "Microsoft JhengHei";
      color: #fff;
      letter-spacing: 4px;
}
header ul li a span {
        display: inline-block;
        margin: 10px 0;
        font-size: 12px;
        color: white;
        letter-spacing: 2px;
}
header ul li a p {
        display: inline-block;
        padding-top: 10px;
}
header ul li a:hover, header ul li a:hover span {
        color: #ffaa00;
}
#main_section {
  padding: 30px;
  background-color: #282f44;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  color: #fff;
  position: relative;
  height: 500px;
  overflow: hidden;
}
#main_section h1 {
    padding-top: 30px;
    font-size: 32px;
    letter-spacing: 2px;
}
#main_section h3 {
    padding-top: 20px;
    font-weight: 200;
    color: #ddd;
    letter-spacing: 2px;
}
#main_section i {
    color: orange;
}
#work_section {
  text-align: center;
  padding-bottom: 30px;
}
#cat {
  position: absolute;
  height: 300px;
  bottom: -5px;
  width: 400px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 100;
}
#fish {
  position: absolute;
}
#fish:after {
    content: "";
    position: absolute;
    display: block;
    height: 20px;
    width: 20px;
    border: 3px solid #fff;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
}

