.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
.mobileTop {
  text-align: center;
  color: #fff;
  background: steelblue;
  position: fixed;
  width: 100%;
  line-height: 50px;
  height: 50px;
  top: 0;
}
.mobileTop i.fa-bars {
    position: absolute;
    left: 5px;
    top: 8px;
    cursor: pointer;
}
.mobile_list {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: rgba(64, 89, 143, 0.8);
  position: fixed;
  top: 50px;
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
  left: -100%;
  color: #fff;
  z-index: 99999;
  text-align: center;
}
.mobile_list ul {
    width: 80%;
    margin: 0 auto;
    padding: 100px 0px;
}
.mobile_list ul li {
      display: block;
      padding: 20px 0;
}
.mobile_list ul li a {
        font-size: 14px;
        color: #ddd;
}
.mobile_list ul li span {
        color: #fff;
        padding-left: 10px;
}
.show {
  left: 0;
}

