.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
.bg {
  width: 100%;
  height: 300px;
  background: url(../../static/img/01.eac3265.jpg) no-repeat center fixed;
  background-size: cover;
  position: absolute;
  top: 0;
}
.contact_container h1 {
  font-size: 40px;
  font-weight: 900;
  text-align: left;
  color: #555;
  padding: 20px 0;
  text-align: center;
  margin-top: 60px;
}
@media all and (max-width: 768px) {
.contact_container h1 {
      font-size: 28px;
}
}
.contact {
  width: 50%;
  margin: 0 auto;
  text-align: center;
  padding: 100px 0;
}
.contact a {
    display: inline-block;
    margin: 10px 30px;
    color: #555;
}
.contact a:first-child {
      width: 100%;
      margin: 10px 0px;
}
.contact a span {
      display: block;
      padding: 10px 0;
}
.contact a img {
      width: 80px;
      -webkit-transition-duration: .5s;
              transition-duration: .5s;
      cursor: pointer;
}
.contact a:hover img {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
}
